<template>
  <v-card class="gameDialog">
    <v-card-title class="headline">New Game</v-card-title>
    <v-card-text>
      <v-radio-group
        label="BoardSize"
        :value="settings.size"
        color="secondary"
        class="gameDialog_radioGroup"
        @change="update('size', $event)"
      >
        <v-radio
          v-for="item in boardSizes"
          :key="item.value"
          :label="item.name"
          :value="item.value"
          class="gameDialog_radio"
          color="secondary"
        ></v-radio>
      </v-radio-group>
      <v-slider
        label="Players"
        :value="settings.players"
        @change="update('players', $event)"
        min="2"
        max="8"
        color="secondary"
        thumb-label="always"
        class="gameDialog_slider"
      ></v-slider>
    </v-card-text>
    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn color="secondary" large @click="newGame">Start</v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
import { mapActions, mapState } from 'vuex'
import GameService from '@/services/game.service'
import { boardSizes } from '@/globals/globals'

export default {
  name: 'GameDialog',
  computed: mapState({
    game: state => state.game,
    settings: state => JSON.parse(JSON.stringify(state.game.settings))
  }),
  data: () => ({
    gameService: new GameService(),
    firstName: 'test',
    boardSizes
  }),
  methods: {
    newGame () {
      this.changeSettings(this.settings)
      this.gameService.startGame()
      this.$emit('close', true)
    },
    update (key, val) {
      this.settings[key] = val
      this.$set(this.settings, key, val)
    },
    ...mapActions('game', ['changeSettings'])
  }
}
</script>

<style lang="scss">
.gameDialog {
  &_radio {
    grid-column-start: 2;
    &Group {
      .v-input--radio-group__input {
        display: grid;
        grid-template-columns: 85px 1fr;
      }
    }
  }
  &_slider {
    margin-top: 30px;
    .v-label {
      width: 65px;
    }
    .v-slider__thumb {
      cursor: pointer;
    }
  }
}
</style>
