<template>
  <div
    class="results_container"
    :class="{'results_container-active' : resultsActive}"
    @click="resultsActive = false"
  >
    <template
      v-if="game.players.length > 1 &&
      game.player &&
      game.results.attacker === game.player.id &&
      game.players[0].id === 0 &&
        (game.results.attackerDice.cell === game.selected || game.results.defenderDice.cell === game.selected || game.selected === null)"
    >
      <div class="results">
        <div
          class="results_side results_side-attacker"
          :class="{'results_side-win' : game.results.attackerDice.total > game.results.defenderDice.total}"
        >
          <div class="results_result">
            <div class="results_dices">
              <div
                class="results_dice"
                :class="['results_dice-' + dice]"
                v-for="(dice, i) in game.results.attackerDice.dice"
                :key="i"
              ></div>
            </div>
          </div>
          <div
            class="results_color"
            :class="['results_color-' + game.results.attacker]"
          >{{game.results.attackerDice.total}}</div>
        </div>
        <div class="results_vs">VS</div>
        <div
          class="results_side results_side-defender"
          :class="{'results_side-win' : game.results.attackerDice.total <= game.results.defenderDice.total}"
        >
          <div
            class="results_color"
            :class="['results_color-' + game.results.defender]"
          >{{game.results.defenderDice.total}}</div>
          <div class="results_result">
            <div class="results_dices">
              <div
                class="results_dice"
                :class="['results_dice-' + dice]"
                v-for="(dice, i) in game.results.defenderDice.dice"
                :key="i"
              ></div>
            </div>
          </div>
        </div>
      </div>
    </template>
    <div v-if="game.players[0] && game.players[0].id !== 0" class="results_message">
      <div class="results_color results_color-0"></div>
      <div class="results_message_text results_message_text-lose">You lost, </div>
      <v-btn color="secondary" class="results_message_btn" large @click="newGame">Get Revenge</v-btn>
    </div>
    <div v-else-if="game.players.length === 1" class="results_message">
      <div class="results_color" :class="['results_color-' + game.results.attacker]"></div>
      <div class="results_message_text results_message_text-win">YOU WIN!</div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import GameService from '@/services/game.service'

export default {
  name: 'GameControls',
  computed: mapState({
    game: state => state.game,
    gameResults: state => state.game.results
  }),
  data: () => ({
    gameService: new GameService(),
    resultsActive: false
  }),
  methods: {
    newGame () {
      this.gameService.startGame()
    }
  },
  watch: {
    gameResults () {
      this.resultsActive = true
    }
  }
}
</script>

