<template>
  <div class="controls">
    <GameResults />
    <div class="controls_players">
      <div
        v-for="(player) in game.players"
        class="controls_player"
        :class="{'controls_player-active': player.id === game.player.id}"
        :key="player.id"
      >
        <div
          class="controls_player_color"
          :class="['controls_player_color-' + player.id]"
        >{{player.size}}</div>
        <div class="controls_player_reserve">(+{{player.reserve}})</div>
      </div>
    </div>
    <div class="controls_actions">
      <v-btn
        color="secondary"
        :disabled="game.players.length < 2 || game.busy"
        @click="nextPlayer"
        large
      >Next turn</v-btn>

      <v-btn-toggle class="controls_btnGroup">
        <v-btn
          color="secondary"
          large
          :disabled="game.busy"
          class="controls_btnGroup_btn controls_btnGroup_btn-newGame"
          @click="newGame"
        >New Game</v-btn>
        <v-dialog v-model="newGameDialog" max-width="290">
          <template v-slot:activator="{ on }">
            <v-btn
              color="secondary"
              :disabled="game.busy"
              large
              v-on="on"
              class="controls_btnGroup_btn controls_settings"
            >
              <v-icon>mdi-cogs</v-icon>
            </v-btn>
          </template>
          <GameDialog v-on:close="newGameDialog = false" />
        </v-dialog>
      </v-btn-toggle>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import GameService from '@/services/game.service'
import GameDialog from './GameDialog.vue'
import GameResults from './GameResults.vue'

export default {
  name: 'GameControls',
  components: {
    GameDialog,
    GameResults
  },
  computed: mapState({
    game: state => state.game
  }),
  data: () => ({
    gameService: new GameService(),
    newGameDialog: false
  }),
  methods: {
    nextPlayer () {
      this.gameService.nextPlayer()
    },
    newGame () {
      this.newGameDialog = false
      this.gameService.startGame()
    }
  }
}
</script>

