    position: relative;
    &:before {
      content: "";
      position: absolute;
      opacity: 0;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background-color: rgba(255, 255, 255, 0.2);
      border-top-left-radius: 6px;
      border-bottom-right-radius: 6px;
      transition: opacity 0.3s cubic-bezier(0.4, 0, 0.6, 1);
    }
    &_color {
      width: 40px;
      height: 40px;
      border-top-left-radius: 6px;
      border-bottom-right-radius: 6px;
      top: -5px;
      left: -5px;
      position: relative;
      color: #fff;
      font-size: 32px;
      line-height: 40px;
      text-align: center;
    }
    &_reserve {
      margin-left: 6px;
    }
    &-active {
      &:before {
        opacity: 1;
      }
    }
  }
  @each $i, $color in $colors {
    &_player {
      &_color {
        &-#{$i} {
          background-color: $color;
        }
      }
    }
  }
  &_actions {
    margin: 30px 20px 0;
    display: flex;
    justify-content: center;
    > * {
      margin: 0 10px;
    }
  }
  &_btnGroup {
    &_btn {
      opacity: 1 !important;
    }
  }
  &_settings {
    min-width: auto !important;
  }
}

