  .controls {
    &_actions {
      flex-direction: column;
      > * {
        width: 100%;
        margin: 0 0 20px;
      }
    }
    &_btnGroup {
      &_btn {
        &-newGame {
          width: calc(100% - 49px);
        }
      }
    }
  }
}
