import Vue from 'vue'

const state = {
  board: [],
  selected: null,
  defender: null,
  player: {},
  players: [],
  adjacents: [],
  settings: {
    players: 4,
    size: 4,
    difficulty: 1
  },
  results: {},
  busy: false
}

// Getter functions
const getters = {
  getBoard () {
    return JSON.parse(JSON.stringify(state.board))
  },
  getCell (i) {
    return JSON.parse(JSON.stringify(state.board[i]))
  },
  getSelected () {
    return JSON.parse(JSON.stringify(state.selected))
  },
  getDefender () {
    return JSON.parse(JSON.stringify(state.defender))
  },
  getPlayer () {
    return JSON.parse(JSON.stringify(state.player))
  },
  getPlayers () {
    return JSON.parse(JSON.stringify(state.players))
  },
  getAdjacents () {
    return JSON.parse(JSON.stringify(state.adjacents))
  },
  getSettings () {
    return JSON.parse(JSON.stringify(state.settings))
  },
  getResults () {
    return JSON.parse(JSON.stringify(state.results))
  },
  getBusy () {
    return JSON.parse(JSON.stringify(state.busy))
  }
}

// Mutations
const mutations = {
  setBoard (state, data) {
    state.board = data
  },
  setCell (state, data) {
    Vue.set(state.board, data.i, data.data)
  },
  setSelected (state, data) {
    state.selected = data
  },
  setDefender (state, data) {
    state.defender = data
  },
  setPlayer (state, data) {
    state.player = data
  },
  setPlayers (state, data) {
    state.players = data
  },
  setAdjacents (state, data) {
    state.adjacents = data
  },
  setSetting (state, data) {
    state.settings = { ...state.settings, ...data }
  },
  setSettings (state, data) {
    state.settings = data
  },
  setResults (state, data) {
    state.results = data
  },
  setBusy (state, data) {
    state.busy = data
  }
}

// Actions
const actions = {
  changeBoard ({ commit }, data) {
    commit('setBoard', data)
  },
  changeCell ({ commit }, data) {
    commit('setCell', data)
  },
  changeSelected ({ commit }, data) {
    commit('setSelected', data)
  },
  changeDefender ({ commit }, data) {
    commit('setDefender', data)
  },
  changePlayer ({ commit }, data) {
    commit('setPlayer', data)
  },
  changePlayers ({ commit }, data) {
    commit('setPlayers', data)
  },
  changeAdjacents ({ commit }, data) {
    commit('setAdjacents', data)
  },
  changeSetting ({ commit }, data) {
    commit('setSetting', data)
  },
  changeSettings ({ commit }, data) {
    commit('setSettings', data)
  },
  changeResults ({ commit }, data) {
    commit('setResults', data)
  },
  changeBusy ({ commit }, data) {
    commit('setBusy', data)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
