<template>
  <div class="home">
    <div class="home_actions">
      <v-btn color="secondary" @click="newGame" large>Play</v-btn>
    </div>
  </div>
</template>

<script>
import GameService from '@/services/game.service'

export default {
  name: 'Home',
  data: () => ({
    gameService: new GameService()
  }),
  methods: {
    newGame () {
      this.gameService.startGame()
      this.$router.push({
        name: 'Game'
      })
    }
  }
}
</script>

